<template>
  <div class="middleware-modulemanage">
    <g-el-table
      :tableHeadShow="tableHeadShow"
      :url="url"
      :type="type"
      :cloumns="cloumns"
      :params="params"
      :pageParams="pageParams"
      @selectChange="selectChange"
      ref="middleware_modulemanage_table"
    >
      <template v-slot:table_header>
        <el-button type="primary" size="mini" @click="reload">刷新</el-button>
      </template>
      <!-- 操作用户名称 -->
      <template v-slot:name_head="props">
        <el-input
          v-model="params.name"
          size="mini"
          placeholder="请输入内容"
          @keyup.enter.native="search"
          :maxLength="20"
        ></el-input>
      </template>
      <!-- 操作类型 -->
      <template slot="type" slot-scope="props">
        <span v-if="props.data.row.type == 'hgshield'">函谷盾</span>
        <span v-if="props.data.row.type == 'client'">客户端</span>
        <span v-if="props.data.row.type == 'netfilter'">Netfilter服务</span>
      </template>
      <template v-slot:type_head="props">
        <el-select v-model="params.type" clearable @change="search" size="mini" placeholder="请选择">
          <el-option
            v-for="item in dropDownData.type"
            :key="item.id"
            :label="item.value"
            :value="item.id"
          ></el-option>
        </el-select>
      </template>
      <!-- 操作IP -->
      <template v-slot:ip_head="props">
        <el-input v-model="params.ip" :maxLength="20" size="mini" placeholder="请输入内容" @keyup.enter.native="search"></el-input>
      </template>
      <!-- 版本号 -->
      <template v-slot:version_head="props">
        <el-input
          v-model="params.version"
          size="mini"
          placeholder="请输入内容"
          @keyup.enter.native="search"
          :maxLength="20"
        ></el-input>
      </template>
       <!-- 描述 -->
      <template v-slot:desc_head="props">
        <el-input
          v-model="params.desc"
          size="mini"
          placeholder="请输入内容"
          @keyup.enter.native="search"
          :maxLength="20"
        ></el-input>
      </template>
    </g-el-table>
  </div>
</template>
<script>

export default {
  data() {
    return {
      url: "/middleware/module/list",
      type: "get",
      cloumns: [
        {
          type: "selection",
          fixed: "left"
        },
        {
          type: "index",
          // fixed: "left",
          label: "序号",
          second_head: {
            type: "index",
            width: "60px",
            label: " ",
            indexMethod: function(index) {
              return index + 1;
            }
          }
        },
        {
          label: "主机名",
          sortable: true,
          prop: "name",
          second_head: {
            width: "150px",
            prop: "name",
            templateHeadName: "name_head"
          }
        },
        {
          label: "IP地址",
          prop: "ip",
          sortMethod: this.$tools.IPSort,
          sortable: true,
          second_head: {
            width: "200px",
            templateHeadName: "ip_head",
            prop: "ip"
          }
        },
        {
          label: "版本号",
          prop: "version",
          sortable: true,
          second_head: {
            width: "200px",
            templateHeadName: "version_head",
            prop: "version"
          }
        },
        {
          label: "类型",
          prop: "type",
          sortable: true,
          second_head: {
            width: "200px",
            isTemplate:true,
            templateName:"type",
            templateHeadName: "type_head",
            prop: "type"
          }
        },
        {
          label: "描述",
          prop: "desc",
          sortable: true,
          second_head: {
            minWidth: "200px",
            templateHeadName: "desc_head",
            prop: "desc"
          }
        }
      ],
      tableHeadShow: true,
      params: {
        page: 1,
        limit: 10,
        name: "",
        ip: "",
        version: "",
        desc:"",
        type:""
      },
      pageParams: {
        layout: "total, sizes, prev, pager, next, jumper",
        pageAlign: "left",
        pageSizes: [10, 20, 30]
      },
      dropDownData:{
        type:[
          {
            id:"netfilter",
            value:"Netfilter服务"
          },
          {
            id:"client",
            value:"客户端"
          },
          {
            id:"hgshield",
            value:"函谷盾"
          }
        ]
      },
      selectData:[]
    };
  },
  methods: {
    reload() {
      this.$refs.middleware_modulemanage_table.reload();
    },
    search() {
      this.params.page = 1
      this.reload()
    },
    selectChange(select) {
      this.selectData = select
    }
  }
};
</script>


